.navbar-container {
    width: 100%;
    height: 60px;
    border-bottom: 2px solid var(--main-color);
    display: grid;
    grid-template-columns: repeat(18, 1fr);
    padding-bottom: 5px;
    margin-bottom: 40px;
    align-items: flex-end;
    background-color: var(--content-bg-color);
}

.navbar-header {
    grid-column-start: 6;
    font-size: 40px;
    color: var(--main-color);
    font-family: Georgia;
}

.navbar-header:hover {
    cursor: pointer;
}

.login-logout-container {
    grid-column-start: 13;
    align-self: center;
}
