.landing-login-button {
    width: 60%;
    height: 60px;
    border: 2px solid var(--main-color);
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.logout-button {
    width: 100px;
    height: 30px;
    border: 2px solid var(--main-color);
    border-radius: 10px;
    justify-content: center;
    align-items: center;
}

.logout-button-text {
    text-align: center;
    color: var(--main-color)
}

.login-button {
    width: 170px;
    height: 30px;
    border: 2px solid var(--main-color);
    border-radius: 10px;
    justify-content: center;
    align-items: center;
}

.login-button-text {
    text-align: center;
}

a {
    text-decoration: none;
    color: var(--main-color);
}

.logout-button:hover, .login-button:hover, .landing-login-button:hover {
    background-color: var(--main-color);
    cursor: pointer;
    transition: background-color 150ms ease;
}

.logout-button:hover .logout-button-text, .login-button:hover h3, .landing-login-button:hover .landing-login-button-text {
    color: white;
}

.outline-button {
    border: 2px solid var(--main-color);
    justify-content: center;
    align-items: center;
}

.get-recipes-button {
    width: 100px;
    height: 30px;
    margin-left: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 8px;
}

.get-recipes-button:hover {
    cursor: pointer;
    background-color: var(--main-color);
}

.get-recipes-button-text {
    color: var(--main-color);
}

.get-recipes-button:hover .get-recipes-button-text {
    color: #FFF;
}

.sample-account-view-profile-button {
    /* background-color: red; */
    padding: 0 5px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.sample-account-view-profile-button-text {
    font-family: Arial;
    font-size: 16px;
    border-bottom: 1px solid transparent;
}

.sample-account-view-profile-button-text:hover {
    border-bottom: 1px solid var(--sample-red);
    color: var(--sample-red);
}

@media screen and (max-width: 1100px) and (min-width: 500px) {
    .landing-login-button {
        margin: auto;
        height: 60px;
        width: 300px;
        font-size: 22px;
    }
}

@media screen and (max-width: 500px) {
    .landing-login-button {
        margin: auto;
        height: 50px;
        width: 200px;
        font-size: 18px;
    }
    .logout-button {
        display: flex;
        align-items: center;
        margin-top: 7px;
        height: 40px;
    }
}
